import React, {Component, setState} from 'react';
import axios from 'axios';
import {Table,Row, Col, Button} from 'react-bootstrap';



export default class PersonList extends Component{
    constructor(props){
        super(props)

    }



    componentDidMount(){
        axios.get("http://localhost:8080/persons")
            .then(res => {
                console.log(res.data);
                this.setState({props:{persons: res.data}});
            }).then(console.log(this.props))
    }

    updateList(props){
        axios.get("http://localhost:8080/persons")
            .then(res => {
                console.log(res.data);
                this.super.setState({persons: res.data});
                }
            )
    }


    removePerson(id){
        axios.delete("http://localhost:8080/persons/"+id,{data: {id: id}})
            .then(res => {
                    console.log(res.data);
                    this.updateList();
                }
            )
    }

    render(){
        return (
            <Row>
                <Col>
                    <Table className='striped bordered hover'>
                        <thead>
                        <tr>
                            <th>#</th>
                            <th>First Name</th>
                            <th>Last Name</th>
                            <th>age</th>
                            <th>  </th>
                        </tr>
                        </thead>

                        <tbody>
                        {
                            this.props.persons
                                .map(person =>
                                    <tr key={person.id}>
                                        <td>{++this.number}</td>
                                        <td>{person.fname}</td>
                                        <td>{person.lname}</td>
                                        <td>{person.age}</td>
                                        <td>
                                            <Button variant="danger" type="submit" onClick={(this.removePerson.bind(person.id)) }>
                                                Delete person
                                            </Button>
                                        </td>
                                    </tr>
                                )}
                        </tbody>

                    </Table>
                </Col>
            </Row>
        )
    }
}

